import moment from 'moment'

const mixin = {
  data () {
    return {
      bgColor: '#323840'
    }
  },
  filters: {
    /**
     * 格式时间
     * @param {*} timestamp 时间戳
     * @param {*} format 格式
     */
    formatTime (timestamp, format) {
      if (!timestamp) return '—'

      return moment(timestamp).format(format || 'YYYY/MM/DD')
    }
  },
  methods: {
    // 跳转
    pageTo (path) {
      this.$router.push(path)
    }
  }
}

export default mixin
